.bottom-bar {
    display: none;
    position: fixed;
    left: 0;
    bottom: 0px;
    width: 100%;
    height: 46px;
    line-height: 46px;
    z-index: 10;
    background: #fff;
    border-top: 1px solid #ebebeb;
    font-size: 15px;
}

.bottom-bar a {
    position: relative;
    float: left;
    width: 50%;
    height: 46px;
    line-height: 46px;
    color: #7e7f80;
    text-align: center;
    display: -webkit-box;
    -webkit-box-pack: center;
    -webkit-box-orient: vertical;
}

.bottom-bar a.float-right {
    float: right;
}

.bottom-bar a:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    border-right: 1px solid #ebebeb;
}

.bottom-bar a.active {
    color: #435c95;
}

.bottom-bar a .cs-icon {
    background-position: 0 -82px;
}

.bottom-bar a.active .cs-icon {
    background-position: 0 -28px;
}

.bottom-bar a .rrc-icon {
    background-position: 0 -55px;
}

.bottom-bar a.active .rrc-icon {
    background-position: 0 0;
}

.bottom-bar a span {
    padding-left: 22px;
    height: 20px;
    line-height: 20px;
